.food-menu {
  text-align: center;
  margin-top: 50px;
}

.food-menu h2 {
  color: #FF5722;
  margin-bottom: 20px;
}

.food-menu ul {
  list-style-type: none;
  padding: 0;
}

.food-menu li {
  margin: 10px auto;
}

.food-menu li button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  width: 200px;
}

.food-menu li button:hover {
  background-color: #45a049;
}